<template>
    <div id="distribuer">
        <!-- les liens s'adapteront à l'utilisateur connecté -->
        <nav class="navbar navbar-expand-md navbar-light">
            <span class="navbar-brand mb-0 h1">Gestion des fournitures scolaires</span>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent"
                aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>

            <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <ul class="navbar-nav mr-auto">
                    <li class="nav-item">
                        <a class="nav-link" href="accueil">Accueil</a>
                    </li>
                    <li class="nav-item dropdown">
                        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true"
                            aria-expanded="false">
                            Consulter
                        </a>
                        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                            <a class="dropdown-item" href="inv-ele">Inventaire d'un élève</a>
                            <a class="dropdown-item" href="inv-ens">Inventaire d'un enseignant</a>
                        </div>
                    </li>
                    <li class="nav-item dropdown active">
                        <a class="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true"
                            aria-expanded="false">
                            Distribuer
                        </a>
                        <div class="dropdown-menu" aria-labelledby="navbarDropdown">
                            <a class="dropdown-item" href="distr-cla">A une classe</a>
                            <a class="dropdown-item" href="distr-ele">A un élève</a>
                            <a class="dropdown-item" href="distr-ens">A un enseignant</a>
                        </div>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="gerer-fourniture">Gérer stock de fourniture</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="gerer-categorie">Gérer les catégories</a>
                    </li>
                </ul>
                <p class="utilisateur">Nom de l'utilisateur connecté</p>                
                <a href="form_conn" class="btn btn-danger" role="button">Déconnexion</a>
            </div>
        </nav>

        <div class="container-fluid col-md-11">


            <h1>Distribuer à une classe</h1>


            <div class="form-group row">
                <select class="form-control col-md">
                    <option>-- Sélectionner une fourniture à ajouter --</option>
                    <optgroup label="Favoris">
                        <option>TGQ</option>
                        <option>DEE</option>
                    </optgroup>
                    <option>Suisse mode d'emploi</option>
                    <option>CO</option>
                    <option>CCS</option>
                </select>

                <select class="form-control col-md">
                    <option>-- Sélectionner un cours --</option>
                    <option>INF3a</option>
                    <option>INF3b</option>
                    <option>HOR2a</option>
                    <option>HOR2b</option>
                </select>
            </div>

            <div class="row">
                <div class="col-md">
                    <button class="btn btn-primary">Ajouter la fourniture sélectionnée</button>
                </div>
                <div class="col-md">
                    <p class="alert alert-info">Cliquez sur le nom d'un élève pour tout cocher/décocher ses fournitures</p>
                </div>
            </div>
            <div class="table-responsive">
                <table class="table table-striped">
                    <thead class="thead-dark">
                        <tr>
                            <th scope="col" width="20"></th>
                            <th scope="col" id="col_fourniture"></th>
                            <th scope="col">Retirer la fourniture</th>
                            <th scope="col" v-for="eleve in eleves" v-bind:key="eleve.id"><button class="btn btn-dark gras"><span class="vertical">{{eleve.nom}}</span></button></th>
                        </tr>
                    </thead>
                    <tbody>
                            <tr v-for="(fourniture) in fournitures" v-bind:key="fourniture.id">
                                <td><i class="fa fa-star-o text-warning"></i></td>
                                <th scope="row" class="col_fourniture">{{fourniture.nom}}<br>
                                    <form><div class="row"><div class="form-group col-md-12">
                                            <label for="quantite">Quantité</label><input value="1" id="quantite" class="form-control form-control-quantite">
                                    </div></div></form>
                                </th>
                                <td>
                                    <button class="btn btn-outline-danger">
                                        Retirer <i class="fa fa-times"></i>
                                    </button>
                                </td>
                                <td v-for="eleve in eleves" v-bind:key="eleve.id">
                                    <input type="checkbox" class="form-check-input position-static" checked v-bind="eleve.key"> <!-- v-bind pour l'instant inutile -->
                                </td>
                            </tr>
                            <tr>
                                <td><i class="fa fa-star text-warning"></i></td>
                                <th scope="row" id="col_fourniture">
                                    <form>
                                        <div class="row"><div class="form-group col-md-12">
                                            <label for="descr">Fiche de dégradation</label>
                                            <input type="text" id="descr" class="form-control" placeholder="Description">
                                            <label for="prix">Prix</label><input type="text" id="prix" class="form-control form-control-prix">
                                            <br>
                                            <label for="quantite">Quantité</label><input value="1" id="quantite" class="form-control form-control-quantite">
                                        </div></div>
                                    </form>
                                </th>
                                <td>
                                    <button class="btn btn-outline-danger">
                                        Retirer <i class="fa fa-times"></i>
                                    </button>
                                </td>
                                <td v-for="eleve in eleves" v-bind:key="eleve.id">
                                    <input type="checkbox" class="form-check-input position-static" checked v-bind="eleve.key"> <!-- v-bind pour l'instant inutile -->
                                </td>
                            </tr>
                        
                    </tbody>
                </table>
            </div>
            <br>
            <button type="button" class="btn btn-primary">Annuler</button>

            <button type="button" class="btn btn-primary">Distribuer</button>
            <br><br><br>
        </div>

    </div>
</template>

<script>
import axios from 'axios';
export default {
    name: 'distribuer',
    data() {
        return {
            eleves: [],
            fournitures: [],
            errors: []
        }
    },
    created() {
        // Récupération des élèves
        axios.get(`http://localhost:3000/eleve`)
        .then(response => {
            // JSON responses are automatically parsed.
            this.eleves = response.data
        })
        .catch(e => {
            this.errors.push(e)
        })
        
        // Récupération des fournitures
        axios.get(`http://localhost:3000/fourniture`)
        .then(response => {
            // JSON responses are automatically parsed.
            this.fournitures = response.data
        })
        .catch(e => {
            this.errors.push(e)
        })
    }

}

</script>

<!-- styling for the component -->
<style scoped>
select {
    margin: 0 15px;
}



td {
    vertical-align: middle;
    text-align: center;
}

.col_fourniture {
    text-align: left;
    min-width: 240px;
}

.form-control-quantite {
    min-width: 0;
    max-width: 100px;
    margin-left: 15px;
    display: inline;
}

.form-control-prix {
    min-width: 0;
    max-width: 100px;
    margin-left: 49px;
    display: inline;
}

.gras {
    font-weight: bold;
    width: 50px;


}

.vertical {
    writing-mode: vertical-lr;
    -webkit-writing-mode: sideways-lr;
    -ms-writing-mode: sideways-lr;
	transform: rotate(180deg);
}

</style>


